<template>
  <section id="home">
    <main v-if="loaded">
      <div class="box">
        <span class="square squareTop"></span>
        <span class="circle circleTop"
          ><font-awesome-icon icon="fa-solid fa-quote-left"
        /></span>

        <p>{{ quote }}</p>
        <span class="circle circleBottom"
          ><font-awesome-icon icon="fa-solid fa-quote-right"
        /></span>
        <span class="square squareBottom"></span>
      </div>
      <h1>{{ author }}</h1>
    </main>
    <ControlButtons v-if="loaded" />
    <Loading v-else />
  </section>
</template>

<script>
import ControlButtons from "@/components/ControlButtons.vue";
import Loading from "@/components/Loading.vue";

export default {
  name: "HomeView",
  components: {
    ControlButtons,
    Loading,
  },
  data() {
    return {
      loaded: false,
    };
  },
  async created() {
    await this.$store.dispatch("GetQuotes");
    this.loaded = true;
  },
  computed: {
    quote() {
      return this.$store.state.quotes[this.$store.state.quoteID].text;
    },
    author() {
      return this.$store.state.quotes[this.$store.state.quoteID].author;
    },
  },
};
</script>
