import Vue from "vue";
import Vuex from "vuex";
import VuexPersistence from "vuex-persist";

Vue.use(Vuex);
const vuexLocal = new VuexPersistence({
  storage: window.localStorage,
});
const url = "https://goquotes-api.herokuapp.com/api/v1/random?count=8261";
//https://goquotes.docs.apiary.io/#reference/get-random-quote(s)/apiv1random/get-random-quote(s)?console=1
export default new Vuex.Store({
  state: {
    quotes: [],
    quoteID: 0,
    likedQuotes: [],
    like: false,
    navigation: false,
  },
  mutations: {
    setQuotes(state, quotes) {
      state.quotes = quotes;
    },
    changeQuoteID(state, x) {
      if (x == 0) {
        if (state.quoteID == 0) {
          state.quoteID = 8260;
        } else {
          state.quoteID--;
        }
      } else {
        if (state.quoteID == 8260) {
          state.quoteID = 0;
        } else {
          state.quoteID++;
        }
      }
    },
    likeQuotes(state, quote) {
      if (!state.likedQuotes.includes(quote)) {
        state.likedQuotes.push(quote);
      } else {
        state.likedQuotes = state.likedQuotes.filter((item) => item !== quote);
      }
    },
    deleteQuote(state, quote) {
      state.likedQuotes = state.likedQuotes.filter((item) => item !== quote);
    },
    navigation(state) {
      state.navigation = !state.navigation;
    },
  },
  actions: {
    async GetQuotes() {
      const response = await fetch(url, {
        method: "GET",
      })
        .then((response) => response.json())
        .then((data) => this.commit("setQuotes", data.quotes));
    },
  },
  plugins: [vuexLocal.plugin],
});
