<template>
  <div id="controlButtons">
    <div class="lefttArrow" @click="changeQuoteID(0), buttonClick()">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="h-5 w-5"
        viewBox="0 0 20 20"
        fill="currentColor"
      >
        <path
          fill-rule="evenodd"
          d="M9.707 16.707a1 1 0 01-1.414 0l-6-6a1 1 0 010-1.414l6-6a1 1 0 011.414 1.414L5.414 9H17a1 1 0 110 2H5.414l4.293 4.293a1 1 0 010 1.414z"
          clip-rule="evenodd"
        />
      </svg>
    </div>
    <div class="heart" @click="likeQuote()">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="h-6 w-6"
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor"
        stroke-width="2"
        v-if="!liked"
      >
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
        />
      </svg>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="h-5 w-5"
        viewBox="0 0 20 20"
        fill="currentColor"
        v-if="liked"
      >
        <path
          fill-rule="evenodd"
          d="M3.172 5.172a4 4 0 015.656 0L10 6.343l1.172-1.171a4 4 0 115.656 5.656L10 17.657l-6.828-6.829a4 4 0 010-5.656z"
          clip-rule="evenodd"
        />
      </svg>
    </div>

    <div class="rightArrow" @click="changeQuoteID(1), buttonClick()">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="h-5 w-5"
        viewBox="0 0 20 20"
        fill="currentColor"
      >
        <path
          fill-rule="evenodd"
          d="M10.293 3.293a1 1 0 011.414 0l6 6a1 1 0 010 1.414l-6 6a1 1 0 01-1.414-1.414L14.586 11H3a1 1 0 110-2h11.586l-4.293-4.293a1 1 0 010-1.414z"
          clip-rule="evenodd"
        />
      </svg>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      liked: false,
    };
  },
  name: "ControlButtons",
  methods: {
    changeQuoteID(x) {
      this.$store.commit("changeQuoteID", x);
    },
    likeQuote() {
      this.$store.commit(
        "likeQuotes",
        this.$store.state.quotes[this.$store.state.quoteID]
      );
      this.liked = !this.liked;
    },
    buttonClick() {
      if (
        this.$store.state.likedQuotes.includes(
          this.$store.state.quotes[this.$store.state.quoteID]
        )
      ) {
        this.liked = true;
      } else {
        this.liked = false;
      }
    },
  },
};
</script>

<style scoped lang="scss">
/*------*/
$orange: #f24b27;
$darkOrange: #5b1809;
@mixin transition() {
  transition: all 0.3s ease;
}
/*------*/

#controlButtons {
  position: fixed;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  div {
    width: 40px;
    height: 40px;

    padding: 20px;

    background: $orange;
    color: white;

    border-radius: 50%;

    @include transition();
    &:hover {
      @include transition();

      background: $darkOrange;

      cursor: pointer;
    }
  }
}
@media (max-width: 768px) {
  #controlButtons {
    div {
      width: 30px;
      height: 30px;
    }
  }
}
</style>
