<template>
  <div id="likedquotes">
    <main>
      <h2 v-if="likedQuotes.length == 0">No quotes in your list</h2>
      <div class="box" v-for="quote in likedQuotes" :key="quote.text">
        <span class="square squareTop"></span>

        <p>{{ quote.text }}</p>
        <span class="delete" @click="deleteQuoteFromList(quote)">
          <div class="icon">
            <font-awesome-icon icon="fa-solid fa-trash-can" />
          </div>
        </span>
        <span class="square squareBottom"></span>
        <h1>{{ quote.author }}</h1>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  name: "LikedQuotes",
  computed: {
    likedQuotes() {
      return this.$store.state.likedQuotes;
    },
  },
  methods: {
    deleteQuoteFromList(quote) {
      this.$store.commit("deleteQuote", quote);
    },
  },
};
</script>

